import React, { useState } from 'react';

import styles from './hero.module.css';
import Logo from '../logo';

const Hero = () => {
  const [active, setActive] = useState(false);
  const isActive = active ? 'is-active' : '';

  const toogleActive = () => {
    setActive(!active);
  };

  return (
    <section className={`hero is-primary is-large home-hero ${styles.hero}`}>
      <div className="hero-head">
        <nav className="navbar">
          <div className="container">
            <div className="navbar-brand">
              <a className={styles.logo}>
                <Logo />
              </a>
              <span
                onClick={toogleActive}
                className={`navbar-burger burger ${isActive}`}
                data-target="navbarMenuHeroB"
              >
                <span></span>
                <span></span>
                <span></span>
              </span>
            </div>
            <div className={`navbar-menu ${isActive}`}>
              <div className="navbar-end">
                <a className="navbar-item">Log In</a>
              </div>
            </div>
          </div>
        </nav>
      </div>

      <div className={`hero-body ${styles.heroBody}`}>
        <h2 className="title is-2 over-title has-text-info">
          Restaurando
          <span className="title-secondary has-text-white">Corpo.</span>
        </h2>
        <h2 className="title over-title has-text-info">
          Revivendo
          <span className="title-secondary has-text-white">Alma.</span>
        </h2>
        <div className="mt-2">
          <button className="button is-info is-outlined">Solicite uma consulta</button>
        </div>
      </div>
    </section>
  );
};

export default Hero;
